<template>
  <div v-loading.fullscreen.lock="loading">
    <div class="chapter" v-if="module">
      <div class="chapter_body" v-if="currentChapter">
        <Description
          v-if="currentChapter.type === 'text'"
          :data="currentChapter"
        />
        <Video
          v-if="currentChapter.type === 'video'"
        />
        <Audio
          v-if="currentChapter.type === 'audio'"
          :data="currentChapter"
        />
        <Presentation
          v-if="currentChapter.type === 'presentation'"
        />
        <div class="chapter_buttons">
          <button
            v-if="getPrev(sidemenu.chapterIndex)" 
            class="btn" @click="changePage('prev')">
            <img src="@/assets/course/arrow-left-icon.svg" alt="arrow-left">
            Назад
          </button>
          <button 
            v-if="getNext(module['themes'][this.sidemenu.index]['materials'],sidemenu.chapterIndex)" 
            class="btn active" 
            @click="changePage('next')">
            Далее
            <img src="@/assets/course/arrow-right-icon.svg" alt="arrow-right">
          </button>
        </div>
      </div>
      <div class="chapter_body" v-if="currentQuiz">
        <Exercise
          :key="quizKey"
          :data="currentQuiz"
        />
      </div>
      <Sidemenu :data="getMenuData" :currentStage="sidemenu" @currentMenu="currentMenu" />
    </div>
  </div>
</template>

<script>
import Description from '../../../../components/course/chapter/Description.vue'
import Presentation from '../../../../components/course/chapter/Presentation.vue'
import Video from '../../../../components/course/chapter/Video.vue'
import Sidemenu from '../../../../components/course/SideMenu.vue'
import Exercise from "@/components/course/chapter/Exercise.vue";
import Audio from '../../../../components/course/chapter/Audio.vue'
export default {
  components: {Exercise, Description, Sidemenu, Video, Presentation, Audio },
    name: 'CourseChapter',

    data() {
        return {
            loading: false,
            currentChapter: null,
            currentQuiz: null,
            quizKey: 0,
            sidemenu: {
                index: 0,
                chapterIndex: 0,
                type: 'chapter'
            },
            module: null
        }
    },
    mounted() {
        const page = this.$route.query.page
        const type = this.$route.query.type
        const chapter = this.$route.query.chapter

        if (page) {
            this.sidemenu.index = Number(page) || 0
        } 
        if (type) {
            this.sidemenu.type = type
        }
        if (chapter) {
            this.sidemenu.chapterIndex = Number(chapter) || 0
        }
        this.getCourseDetails()
    },
    computed: {
        getMenuData() {
            return {
                title: this.module.title,
                type: 'chapter',
                menu: null,
                topics: this.module['themes'],
            }
        }
    },
    methods: {
        async getCourseDetails() {
            this.loading = true
            const apiRoot = API_ROOT
            const slug = this.$route.params.slug
            const moduleId = Number(this.$route.params.chapter)
            await this.$http.get(`${apiRoot}/api/course/${slug}`)
                .then(({ body }) => {
                    const currentModule = body.modules.find(module => module.id === moduleId)
                    if (currentModule) {
                        this.module = currentModule
                        this.currentMenu(this.sidemenu)
                    }
                })
                .catch(err => {
                    if (err.status === 404) {
                        this.$router.push({ name: 'not-found' })
                    }
                    console.log(err)
                })
              .finally(() => {
                  this.loading = false
              })
        },
        currentMenu(data) {
            this.sidemenu.index = data.index
            this.sidemenu.chapterIndex = data.chapterIndex

            if (data.type === 'quiz') {
              this.currentChapter = null;
              this.currentQuiz = this.module['themes'][data.index]['quizzes'][data.chapterIndex];
              this.quizKey++;
            }

            if (data.type === 'chapter') {
              this.currentChapter = this.module['themes'][data.index]['materials'][data.chapterIndex];
              this.currentQuiz = null;

              const materialId = this.currentChapter && this.currentChapter.id ? this.currentChapter.id : false
              const timeout = this.currentChapter && this.currentChapter.log_timeout ? this.currentChapter.log_timeout : 0
              const apiRoot = API_ROOT;
              const slug = this.$route.params.slug
              if (!this.currentChapter.finished) {
                setTimeout(() => {
                  if (this.routeLeft) return false;
                  this.$http.get(`${apiRoot}/api/course/${this.$route.params.slug}`)
                    .then(res => {
                      for (let i = 0; i < res.body.modules.length; i++) {
                        if (res.body.modules[i].id == this.currentModuleId) {
                          if (res.body.modules[i + 1].id == undefined) {


                          }
                        }
                      }

                    })
                    .catch(err => {
                      if (err.status === 404) {
                        this.$router.push({name: 'not-found'})
                      }
                      console.log(err)
                    })
                    .finally(() => {

                    })


                  this.$http.post(`${apiRoot}/api/course/material/finish/${materialId}`).then(() => {
                    this.$http.post(`${apiRoot}/api/user/change-coins`, {"count": 1}).then(() => {
                    })
                    this.$http.get(`${apiRoot}/api/course/${slug}`).then(res => {
                      this.course = res.body
                    })
                  })
                }, timeout * 10 * 60)
              }
            }
        },


        changePage(type) {
            const topics = this.module['themes']
            let chapters = this.module['themes'][this.sidemenu.index]['materials']
            let topicIndex = this.sidemenu.index
            let chapterIndex = this.sidemenu.chapterIndex


            if (type === 'next') {
                if (this.getNext(chapters, chapterIndex)) {
                    chapterIndex += 1
                } else if (this.getNext(topics, topicIndex)) {
                    topicIndex += 1
                    chapterIndex = 0
                }
            } else if (type === 'prev') {

                if (this.getPrev(chapterIndex)) {
                    chapterIndex -= 1

                } else if (this.getPrev(topicIndex)) {
                    topicIndex -= 1
                    chapters = this.lesson.topics[topicIndex].chapters
                    chapterIndex = chapters.length - 1
                }
            }

            this.currentMenu({index: topicIndex, chapterIndex: chapterIndex})
        },

        getNext(items, itemIndex) {
            const length = items.length
            if (items.length > itemIndex + 1) {
                return itemIndex + 1
            } else {
                false
            }
        },

        getPrev(itemIndex) {
            return itemIndex - 1 >= 0
        },
    }

}
</script>

