    display: flex;
    align-items: flex-start;
    gap: 32px;

    &_body {
        width: calc(100% - 432px);
    }

    &_buttons {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 24px;
        margin-top: 32px;
    }

    .btn {
        background: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 12px;
        width: 240px;
        height: 52px;
        border-radius: 10px;

        &.active {
            background: #007BFC;
            color: #fff;
        }
    }
}
</style>
