      <i class="el-icon-loading"></i>
    </div>
  </div>
</template>

<script>
import Vue from "vue";

export default {
  name: 'Exercise',
  props: ['data'],
  data() {
    return {
      apiRoot: API_ROOT,
      quiz: null,
      loading: false,
      success: false,
      error: false,
      finished: false,
      result: 0,
      percent: 0,
      max_score: 0,
      passing_score: 0,
      quizEnded: false,
      triesAvailable: true,
      currentQuestionId: 0,
      selectedAnswer: [],
      selectedAnswerRaw: null,
    }
  },
  mounted() {
    this.getQuiz()
  },
  methods: {
    getQuiz() {
      const slug = this.$route.params.slug
      const quiz = this.data.id
      this.$http.get(`${this.apiRoot}/api/course/${slug}/quiz/${quiz}`)
        .then(res => {
          this.quiz = res['body']
          this.triesAvailable = this.quiz['user_tries'] > 0
          this.loading = false
        })
        .catch(err => console.log(err))
        .finally(() => {
          this.loading = false
        })
    },
    saveAnswer() {
      if (this.quizEnded)
        return;

      let params = (this.selectedAnswerRaw !== null) ? {
        answer: this.selectedAnswerRaw,
        answer_raw: this.selectedAnswerRaw,
        question_id: this.currentQuestion.id,
        quiz_id: this.quiz.id
      } : {
        answer: this.selectedAnswer,
        question_id: this.currentQuestion.id,
        quiz_id: this.quiz.id
      }
      this.$http.post(`${this.apiRoot}/api/course/quiz/answer/store`, params).then(res => {
        // if (res.body.message === 'saved')
        //   this.success = true

        // setTimeout(() => {
        //   this.success = false
        // }, 1000)
        // Vue.toastr({
        //   message: 'Success',
        //   description: 'Ответ сохранен',
        //   type: 'success'
        // })
      }).catch(err => {
        // if (err.body.message === 'not_saved')
        //   this.error = true

        // setTimeout(() => {
        //   this.error = false
        // }, 1000)
        // Vue.toastr({
        //   message: 'Error',
        //   description: 'Не удалось сохранить ответ',
        //   type: 'error'
        // })
      }).finally(() => {
        this.getQuiz(false)
      })
    },
    prev() {
      if (!this.currentQuestionId) return;
      if (this.selectedAnswerRaw !== 'null' || this.selectedAnswer.length) {
        this.saveAnswer();
      }
      this.currentQuestionId--
      this.error = false
      this.success = false
      this.selectedAnswer = []
      this.selectedAnswerRaw = null
      if (this.currentQuestion.user_answer) {
        if (this.currentQuestion.user_answer.answer) {
          if (this.currentQuestion.user_answer.answer.length == 1) {
            this.selectedAnswer = this.currentQuestion.user_answer.answer[0]
          } else {
            this.selectedAnswer = this.currentQuestion.user_answer.answer
          }
        }
        if (this.currentQuestion.user_answer.answer_raw) {
          this.selectedAnswerRaw = this.currentQuestion.user_answer.answer_raw
        }
      }
    },
    next() {
      if (typeof this.selectedAnswer=='object'){
        if (this.selectedAnswer.length == 0){
          this.selectedAnswer = ''
        }
      }
      if (this.selectedAnswer) {
        this.saveAnswer();
        this.currentQuestionId++
        this.error = false
        this.success = false
        this.selectedAnswer = []
        this.selectedAnswerRaw = null
        return
      }
      Vue.toastr({
        description: 'Необходимо выбрать ответ',
        type: 'error'
      })
    },
    finish() {
      if (typeof this.selectedAnswer == 'object' && !this.selectedAnswer.length)
        this.selectedAnswer = ''

      if (this.selectedAnswer) {
        this.saveAnswer();
      } else {
        Vue.toastr({
          description: 'Необходимо выбрать ответ',
          type: 'error'
        })
        return
      }

      if (this.quiz.mode === 'DEFAULT' || this.quiz.mode === 'ONLY_RAW_ANSWERS') {
        this.$http.get(`${this.apiRoot}/api/course/quiz/get-user-answers/${this.quiz.id}`)
          .then(res => {
            this.quizEnded = res['body']['coin_credited']
            this.results = res['body']['data']
            this.result = res['body']['result']
            this.passing_score = res['body']['passing_score']
            this.percent = res['body']['percent']
            this.max_score = res['body']['max_score']
            this.error = false
            this.success = false
            this.finished = true
          }).catch(err => {
          Vue.toastr({
            message: 'Error',
            description: 'Не удалось завершить тест',
            type: 'error'
          })
        })
          .finally(() => {
              this.getQuiz(true, true)
              // this.quizEnded = true

              if (this.finished && this.result >= this.quiz.passing_score) {
                if (this.quizEnded == false) {
                  this.$http.post(`${this.apiRoot}/api/user/change-coins`, {
                    "count": 1,
                    'quiz_id': this.quiz.id
                  }).then(() => {
                  })
                }
                this.$http.get(`${window.API_ROOT}/api/course/${this.$route.params.slug}`)
                  .then(res => {
                  })
                  .catch(err => {
                    if (err.status === 404) {
                      this.$router.push({name: 'not-found'})
                    }
                    console.log(err)
                  })
                  .finally(() => {
                  })
              }
            }
          )
      } else {
        this.$router.push({
          name: 'course-module',
          params: {
            slug: this.$route.params.slug,
            module: this.currentModuleId
          }
        })
      }
      this.showResults()
    },
    showResults(finished = true) {
      this.$http.get(`${this.apiRoot}/api/course/quiz/get-user-answers/${this.quiz.id}`)
        .then(res => {
          this.results = res['body']['data']
          this.result = res['body']['result']
          this.passing_score = res['body']['passing_score']
          this.percent = res['body']['percent']
          this.max_score = res['body']['max_score']
          this.error = false
          this.success = false
          this.finished = finished
        }).catch(err => {
        Vue.toastr({
          message: 'Error',
          description: 'Не удалось завершить тест',
          type: 'error'
        })
      })
        .finally(() => {
        })
    },
  },
  computed: {
    currentQuestion() {
      if (!this.quiz['questions']) {
        return;
      }
      let item = this.quiz['questions'].find((question, key) => {
        return key == this.currentQuestionId;
      })
      if (item['user_answer']) {
        if (item['user_answer'].answer) {
          if (item['user_answer'].answer.length == 1) {
            this.selectedAnswer = item['user_answer'].answer[0]
          } else {
            this.selectedAnswer = item['user_answer'].answer
          }
        }
        if (item['user_answer'].answer_raw) {
          this.selectedAnswerRaw = item['user_answer'].answer_raw
        }
      }
      return item;
    },
    lastQuestion: function () {
      return this.currentQuestionId + 1 === this.quiz['questions'].length;
    },
    firstQuestion: function () {
      return this.currentQuestionId == 0;
    }
  }
}
</script>

